import axios from 'axios'
import { Alert } from 'vue-ydui/dist/lib.rem/dialog'
import configs from '../configs'

axios.interceptors.request.use(config => {
  return config;
}, err => {
  Alert({ mes: '请求超时!' })
  return Promise.resolve(err);
})

axios.interceptors.response.use(data => {
  if (data.status && data.status == 200 && data.data.status == 'error') {
    Alert({ mes: data.data.msg })
    return;
  }
  return data;
}, err => {
  if (err.response) {
    if (err.response.status == 504 || err.response.status == 404) {
      Alert({ mes: '服务器错误⊙﹏⊙∥' })
    } else if (err.response.status == 403) {
      Alert({ mes: '权限不足,请联系管理员!' })
    } else {
      Alert({ mes: '未知错误!' });
    }
    return Promise.resolve(err);
  } else {
    Alert({ mes: '未知错误!' });
  }
})

export const postRequest = (url, params, isLogin, isUser) => {
  let urlp;
  if (isLogin) { //是否登录调用
    urlp = `${configs.loginAPI}${url}`
  } else {
    if (url.split('/').length > 2) {
      urlp = `${configs.api}${url}`
    } else {
      if (isUser) {
        urlp = `${configs.userAPI}${url}`
      } else {
        urlp = `${configs.rootAPI}${url}`
      }
    }
  }
  // 获取token
  let userInfo = JSON.parse(localStorage.getItem('userInfo'));
  if (userInfo) {
    params.token = userInfo.token
  }

  return axios({
    method: 'post',
    url: urlp,
    data: params,
    transformRequest: [function(data) {
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export const getRequest = (url) => {
  return axios({
    method: 'get',
    url: `${configs.api}${url}`
  });
}

export const uploadFileRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${configs.rootAPI}${url}`,
    data: params,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
}

export const putRequest = (url, params) => {
  return axios({
    method: 'put',
    url: `${configs.rootAPI}${url}`,
    data: params,
    transformRequest: [function(data) {
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export const deleteRequest = (url) => {
  return axios({
    method: 'delete',
    url: `${configs.rootAPI}${url}`
  });
}



